
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapState } from 'vuex'
export default {
components: {
Group,
XInput,
XButton,
XDialog,
Spinner,
Toast
},
created () {
console.log('query id', this.$route.query.tableId)
},
data () {
return {
username: ''
}
},
computed: {
isDisable () {
return this.username === ''
},
...mapState({
isLogin: state => state.user.isLogin,
isLoginFail: state => state.user.isLoginFail
})
},
methods: {
login () {
this.$store.dispatch('user/login', {
username: this.username,
tableId: this.$route.query.tableId
})
}
}
}



