export default {
  props: {
    dish: {
      type: Object
    }
  },
  methods: {
    increaseCart (event) {
      console.log('asdasd')
      this.$store.commit('menu/increaseCart', {
        dish: this.dish
      })
    },
    decreaseCart (event) {
      this.$store.commit('menu/decreaseCart', {
        dish: this.dish
      })
    }
  }
}



