
import XDialog from 'vux/src/components/x-dialog/index.vue'

import cartControl from '@/components/cart-control/cart-control'
export default {
components: {
XDialog,
cartControl
},
props: {
showDD: {
Boolean
}
},
created () {
console.log('dish-detail show', this.dish.show)
},
data () {
return {
}
},
methods: {
addOne () {
this.$store.commit('menu/increaseCart', {
dish: this.dish
})
this.dish.count = 1
},
hide () {
this.$emit('hide')
}
},
computed: {
dish () {
return this.$store.state.menu.selectedDish
}
}
}



