
import BScroll from 'better-scroll'
import Toast from 'vux/src/components/toast/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'

import { mapGetters } from 'vuex'

export default {
components: {
Toast,
XCircle
},
data () {
return {
order: {},
urged: false
}
},
created () {
console.log('OrderDetails ', this.getOrderById(this.$route.params.orderId))
this.order = this.getOrderById(this.$route.params.orderId)
},
computed: {
totalPrice: function () {
let total = 0
for (let i in this.order.dishes) {
let price = this.order.dishes[i].price
let count = this.order.orderItems[i].quantity
total += price * count
}
return total
},
...mapGetters('order', {
getOrderById: 'getOrderById'
})
},
methods: {
back () {
this.$router.back(-1)
},
async urge (event, orderId, dishId) {
if (event.target.innerText === '') {
this.urged = true
await this.$store.dispatch('order/urgeOrder', orderId, dishId)
event.target.innerText = ''
}
},
calculateWaitingPercent (payDate) {
return 80
},
getCurrentOrderItemState (orderItem) {
if (orderItem.finished) {
return ''
}
if (orderItem.urge) {
return ''
}
let currentPercent = this.calculateWaitingPercent(this.order.payDate)
if (!orderItem.urge && currentPercent === 100) {
return ''
}
return ''
}
},

mounted () {
this.$nextTick(() => {
if (!this.scroll) {
this.scroll = new BScroll(this.$refs.orderDetailsWrapper, {
scrollY: true,
click: true
})
} else {
this.scroll.refresh()
}
})
for (let i in this.order.dishes) {
let time = setInterval(() => {
if (this.order.dishes[i].waitingPercent === 100) {
clearInterval(time)
this.order.dishes[i].state = ''
}
this.order.dishes[i].waitingPercent++
}, 1000)
}
}
}



