
import CartControl from '../cart-control/cart-control'
import BScroll from 'better-scroll'
import Toast from 'vux/src/components/toast/index.vue'

import { mapState, mapGetters } from 'vuex'
export default {
components: {
CartControl,
Toast
},
data () {
return {
show: false,
showToast: false
}
},
computed: {
...mapGetters({
totalPrice: 'menu/totalPrice'
}),
listshow () {
if (this.show) {
this.$nextTick(() => {
if (!this.scroll) {
this.scroll = new BScroll(this.$refs.shopWrapper, {
startY: 0,
click: true
})
this.scroll.minScrollY = 0
this.scroll.on('scrollEnd', (payload) => {
console.log(payload)
})
console.log('asdasd')
} else {
this.scroll.refresh()
}
})
}
return this.show
},
...mapState({
dishsSelect: state => state.menu.selectDishes
})
},
methods: {
shows (target) {
console.log(target)
},
showList () {
if (this.totalPrice > 0) {
this.show = !this.show
}
console.log('"show list"')
console.log(this.scroll)
},
closeList () {
this.show = false
console.log('"close list"')
},
toPayment () {
if (this.totalPrice > 0) {
this.$router.push('/pay')
} else {
this.showToast = true
}
},
empty () {
this.$store.commit('menu/emptySeletedDish')
this.show = false
}
}
}



